import request from '@/utils/request'

// 获取个人信息
export function getUserProfile () {
  return request({
    url: 'user'
  })
}

export function getUserInfo () {
  return request({
    url: 'user/profile'
  })
}

// 修改用户个人信息
export function editUserInfo (user) {
  return request({
    url: 'user/profile',
    method: 'PATCH',
    data: user
  })
}

// 修改头像
export function editUserPhoto (formData) {
  return request({
    url: 'user/photo',
    method: 'PATCH',
    data: formData
  })
}
